import React, { Component, Suspense, lazy } from 'react';
import { fetchMovieById } from '../services/api-service';
import { NavLink, Route } from 'react-router-dom';
// import Cast from '../components/Cast';
// import Reviews from '../components/Reviews';
import routes from '../routes';

const BASE_POSTER_URL = 'https://image.tmdb.org/t/p/w300';
const Cast = lazy(() =>
  import('../components/Cast' /* webpackChunkName: "cast" */),
);
const Reviews = lazy(() =>
  import('../components/Reviews' /* webpackChunkName: "reviews" */),
);

class MovieDetailsPage extends Component {
  state = {
    overview: '',
    id: null,
    poster_path: '',
    title: '',
    reviews: [],
    cast: [],
  };

  async componentDidMount() {
    const { movieId } = this.props.match.params;
    const response = await fetchMovieById(movieId);
    console.log(response.data);
    const {
      overview,
      id,
      poster_path,
      title,
      reviews: { results },
      credits: { cast },
    } = response.data;

    this.setState({ overview, id, poster_path, title, reviews: results, cast });
  }

  handleGoBack = () => {
    const { location, history } = this.props;
    history.push(location?.state?.from || routes.home);
  };

  render() {
    const { overview, poster_path, title, reviews, cast } = this.state;
    const { match } = this.props;
    const { handleGoBack } = this;

    return (
      <>
        <button type="button" onClick={handleGoBack}>
          Back
        </button>
        <h2>{title}</h2>
        <img src={poster_path && `${BASE_POSTER_URL}${poster_path}`} alt="" />
        <p>{overview}</p>

        <div>
          <NavLink to={`${match.url}/cast`}>
            <span>Cast</span>
          </NavLink>
          <NavLink to={`${match.url}/reviews`}>
            <span>Reviews</span>
          </NavLink>
        </div>

        <Suspense fallback={<h1>Loading...</h1>}>
          <Route
            path={`${match.path}/cast`}
            render={props => {
              return <Cast {...props} cast={cast} />;
            }}
          />
          <Route
            path={`${match.path}/reviews`}
            render={props => <Reviews {...props} reviews={reviews} />}
          />
        </Suspense>
      </>
    );
  }
}

export default MovieDetailsPage;
